'use client';

import { useEffect, useState } from 'react';
import { Button } from '@/src/components/ui/button';
import { Calendar } from '@/src/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/src/components/ui/popover';
import { Label } from '@/src/components/ui/label';
import { Input } from '@/src/components/ui/input';
import { format } from 'date-fns';
import { vi } from 'date-fns/locale'; // üáªüá≥ n·∫øu c·∫ßn ti·∫øng Vi·ªát
import { cn } from '@/src/lib/utils'; // üì¶ c·∫ßn h√†m cn ƒë·ªÉ n·ªëi className n·∫øu c√≥

interface TimePickerProps {
  onTimeSelected: (timeRange: { startDate: string; rentalDays: number; endDate: string }) => void;
}

export default function TimePicker({ onTimeSelected }: TimePickerProps) {
  const now = new Date();
  const [selectedDate, setSelectedDate] = useState<Date>(now);
  const [rentalDays, setRentalDays] = useState(1);
  const [time, setTime] = useState({ hour: now.getHours(), minute: now.getMinutes() });

  const formatDateTimeLocal = (date: Date) => {
    const yyyy = date.getFullYear();
    const MM = String(date.getMonth() + 1).padStart(2, '0');
    const dd = String(date.getDate()).padStart(2, '0');
    const hh = String(time.hour).padStart(2, '0');
    const mm = String(time.minute).padStart(2, '0');
    return `${yyyy}-${MM}-${dd}T${hh}:${mm}`;
  };

  const calculateEndDate = (start: string, days: number) => {
    const startDateObj = new Date(start);
    startDateObj.setDate(startDateObj.getDate() + days);
    return formatDateTimeLocal(startDateObj);
  };

  useEffect(() => {
    const startDateFormatted = formatDateTimeLocal(selectedDate);
    const endDate = calculateEndDate(startDateFormatted, rentalDays);
    onTimeSelected({ startDate: startDateFormatted, rentalDays, endDate });
  }, [selectedDate, time, rentalDays, onTimeSelected]);

  return (
    <div className="space-y-6">
      {/* Calendar Picker */}
      <div className="space-y-2">
        <Label>Start Date</Label>
        <Popover>
          <PopoverTrigger asChild>
            <Button variant="outline" className={cn('w-full justify-start text-left')}>
              {selectedDate ? format(selectedDate, 'dd/MM/yyyy', { locale: vi }) : 'Pick a date'}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0">
            <Calendar
              mode="single"
              selected={selectedDate}
              onSelect={(date) => date && setSelectedDate(date)}
              initialFocus
            />
          </PopoverContent>
        </Popover>
      </div>

      {/* Time Picker */}
      <div className="grid grid-cols-2 gap-2">
        <div className="space-y-2">
          <Label>Hour</Label>
          <Input
            type="number"
            min={0}
            max={23}
            value={time.hour}
            onChange={(e) => setTime((prev) => ({ ...prev, hour: Number(e.target.value) }))}
          />
        </div>
        <div className="space-y-2">
          <Label>Minute</Label>
          <Input
            type="number"
            min={0}
            max={59}
            value={time.minute}
            onChange={(e) => setTime((prev) => ({ ...prev, minute: Number(e.target.value) }))}
          />
        </div>
      </div>

      {/* Rental Days */}
      <div className="space-y-2">
        <Label>Rental Days</Label>
        <Input
          type="number"
          min={1}
          value={rentalDays}
          onChange={(e) => setRentalDays(parseInt(e.target.value) || 1)}
        />
      </div>
    </div>
  );
}
